<template>
  <div class="lunch">
    <template v-if="!submitted">
      <h1>What would you like for lunch?</h1>
      <co-form :model="lunchForm" :rules="rules" class="lunch__form" ref="lunchForm">
        <div class="lunch__order-details">
          <h2>1. Order details</h2>
          <co-form-item required label="Your name" prop="name">
            <co-input v-model="lunchForm.name"></co-input>
          </co-form-item>
          <co-form-item required label="Your country" prop="country">
            <co-select placeholder="Select country" v-model="lunchForm.country">
              <co-option value="Australia">Australia</co-option>
              <co-option value="New Zealand">New Zealand</co-option>
              <co-option value="Canada">Canada</co-option>
              <co-option value="China">China</co-option>
              <co-option value="United States">United States</co-option>
            </co-select>
          </co-form-item>
          <co-form-item required label="Delivery date" prop="deliveryDate">
            <co-date-picker v-model="lunchForm.deliveryDate" type="date" placeholder="Select date"></co-date-picker>
          </co-form-item>
          <co-form-item required label="Delivery time" prop="deliveryTime">
            <co-time-select
              v-model="lunchForm.deliveryTime"
              :picker-options="{
                        start: '08:30',
                        step: '00:15',
                        end: '18:30'
                    }"
              placeholder="Select time"
            ></co-time-select>
          </co-form-item>
        </div>

        <div class="lunch__your-meal">
          <h2>2. Your meal</h2>
          <co-form-item required label="Meal options" prop="mealOptions">
            <co-radio-group v-model="lunchForm.mealOptions">
              <co-radio label="Vegetarian">Vegetarian</co-radio>
              <co-radio label="Chicken">Chicken</co-radio>
              <co-radio label="Beef">Beef</co-radio>
            </co-radio-group>
          </co-form-item>
          <co-form-item label="Sides">
            <co-checkbox-group v-model="lunchForm.sides">
              <co-checkbox label="Bread roll"></co-checkbox>
              <co-checkbox label="Greek salad"></co-checkbox>
              <co-checkbox label="Chocolate cake"></co-checkbox>
            </co-checkbox-group>
          </co-form-item>
          <co-form-item required label="Cutlery" tooltip="Cutlery is made from recycled plastic">
            <co-switch v-model="lunchForm.cutlery" yes-no></co-switch>
          </co-form-item>
          <co-form-item label="Comments">
            <co-input v-model="lunchForm.comments" type="textarea"></co-input>
          </co-form-item>
        </div>
      </co-form>
      <co-dropdown
        type="primary"
        split-button
        trigger="click"
        @click="handleClick"
        @command="handleClick"
      >
        Pay by card
        <co-dropdown-menu slot="dropdown" @menuItemClick="handleClick">
          <co-dropdown-item command="paypal">Pay by paypal</co-dropdown-item>
          <co-dropdown-item command="cash">Pay with cash</co-dropdown-item>
        </co-dropdown-menu>
      </co-dropdown>
    </template>
    <template v-if="submitted">
      <h1>Thank you for ordering</h1>
    </template>
  </div>
</template>

<script>
export default {
  name: "Lunch",
  data() {
    return {
      submitted: false,
      lunchForm: {
        name: "",
        country: "",
        deliveryDate: "",
        deliveryTime: "",
        mealOptions: "",
        sides: [],
        cutlery: false,
        comments: ""
      },
      rules: {
        name: [
          { required: true, message: "Please enter your name", trigger: "blur" }
        ],
        deliveryTime: [
          {
            required: true,
            message: "Please select delivery time",
            trigger: "change"
          }
        ],
        deliveryDate: [
          {
            required: true,
            message: "Please select delivery date",
            trigger: "change"
          }
        ],
        country: [
          {
            required: true,
            message: "Please select your country",
            trigger: "change"
          }
        ],
        mealOptions: [
          {
            required: true,
            message: "Please select your meal option"
          }
        ]
      }
    };
  },
  methods: {
    handleClick() {
      this.$refs.lunchForm.validate(valid => {
        if (valid) {
          this.submitted = true;
          return;
        }
        return false;
      });
    }
  }
};
</script>

